import { getOctokit } from "@actions/github";

export const getApi = ({ token, owner, repo }) => {
  /**
   * https://octokit.github.io/rest.js/v18
   **/
  const octokit = new getOctokit(token);

  const deleteRunById = async ({ id }) => {
    console.info(`Deleting workflow run #${id}`);

    // return octokit.rest.actions
    //   .deleteWorkflowRun({ owner, repo, run_id: id })
    //   .catch((err) => `An error occurrend: ${err.message}`);
    return [1, 1, 1];
  };

  const deleteRuns = async (runs) => {
    return await Promise.all(runs.map(this.deleteRunById));
  };

  // getWorkflows: async () => {
  //   return await octokit.paginate(
  //     "GET /repos/:owner/:repo/actions/workflows",
  //     { owner, repo }
  //   );
  // },

  const listWorkflowRunsForRepo = async ({
    page = 1,
    status = "completed",
  }) => {
    const r = octokit.rest.actions.listWorkflowRunsForRepo({
      owner,
      repo,
      status,
      page,
      per_page: 100,
    });

    console.info({ listWorkflowRunsForRepo: r });
    return r;
  };

  return {
    deleteRuns,
    deleteRunById,
    listWorkflowRunsForRepo,
  };
};
